@import "./global.scss";

.main {
  width: $width;
  max-width: 1025px;
  min-height: calc(100vh - 5vw);
  margin: 0 auto;
  padding-top: 5vw;
  position: relative;
  z-index: 10;

  .main-innerbox {
    display: flex;
    justify-content: space-between;

    @media screen and (max-width: 1024px) {
      display: block;
    }
  }
}
